body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.navMenu.hide {
width: 0;
border: 0 !important;

}

.navMenu.display {
width: 20%;
overflow: hidden;

}

#selHomeNav, #selAboutNav, #selProjectsNav, #selContactNav {
  font-family: 'Bangers', cursive !important;
  font-weight: lighter; 
  color: whitesmoke;

}

#navMenuHome {
  background: -webkit-linear-gradient(135deg, whitesmoke, #000000);
  background: linear-gradient(-45deg, whitesmoke, #000000);
  border: 1px solid black;

}


#navMenuAbout {
  background: -webkit-linear-gradient(135deg, #9e0012, #000000);
  background: linear-gradient(-45deg, #9e0012, #000000);
  border: 1px solid #9e0012;

}

#navMenuProjects {
  background: -webkit-linear-gradient(135deg, #2d8e39, #000000);
  background: linear-gradient(-45deg, #2d8e39, #000000);
  border: 1px solid #2d8e39;

}

#navMenuContact {
  background: -webkit-linear-gradient(135deg, #00329e, #000000);
  background: linear-gradient(-45deg, #00329e, #000000);
  border: 1px solid #00329e;

}

.navMenu {
height: 100%;
position: fixed;
top: 0;
left: 0;
-webkit-transition: 0.5s;
transition: 0.5s;
overflow: scroll;
z-index: 10;

}

.navMenu p {
padding: 8px 8px 8px 32px;
text-decoration: none;
color: lightgray;
display: block;
-webkit-transition: 0.3s;
transition: 0.3s;
}

.navMenu p:hover {
font-weight: bolder;
color: whitesmoke;
}

.navMenuSelect {
font-size: 25px;
}

.menuItems {
margin-top: 30%;
}

.navClose {
position: absolute;
top: 2%;
right: 5%;
font-size: 20px;
margin-left: 50px;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

  .navMenu.display {
  width: 60%;
  overflow: hidden;
  }
  .navMenuSelect {
  font-size: 20px;
  }

  .navClose {
  position: absolute;
  top: 1.5%;
  right: 8%;
  font-size: 15px;
  margin-left: 50px;
  }

}


@media only screen and (min-device-width: 480px) and (max-device-width: 1050px) {

  .navMenu.display {
  width: 30%;
  overflow: hidden;
  }
}
.mainHome.hide {
visibility: hidden;
}

.mainHome.display {
visibility: visible;
}

.hiThere, .welcomeSection {
font-family: 'Bangers', cursive !important;
font-weight: lighter; 
}

.hiThere {
font-size: 50px;
}

.welcomeSection {
margin-bottom: 10%;
}

.homeContent {
font-size: 25px;
margin-bottom: 5%;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

    .hiThere {
    font-size: 35px;
    }


    .homeContent {
    font-size: 18px;
    margin-bottom: 5%;
    }

}


.mainAbout.hide {
visibility: hidden;
}

.mainAbout.display {
visibility: visible;
}

.aboutContent {
font-size: 25px;
margin-bottom: 5%;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

    .aboutContent {
    font-size: 18px;
    margin-bottom: 5%;
    }

}


.mainContact.hide {
    visibility: hidden;
}
   
.mainContact.display {
visibility: visible;
}


.contactContent {
    font-size: 25px;
    margin-bottom: 5%;
  }
  
  
@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {
  
    .contactContent {
        font-size: 18px;
        margin-bottom: 5%;
      }
  
  }
.projectsHome.hide {
visibility: hidden;
}

.projectsHome.display {
visibility: visible;
}

.projectTitle {
font-size: 25px;
}

.projectContent {
font-size: 25px;
margin-bottom: 12%;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

    .projectTitle {
    font-size: 20px;
    }


    .projectContent {
    font-size: 18px;
    }

}
* {
box-sizing: border-box;
margin: 0;
padding: 0;
}

body {
background-color: #EEE;
font-family: 'Ubuntu Mono', monospace;
font-size: 20px;
padding: 25px;
margin: 0;
overflow: auto;
background-color: #e8e8e8;
}

.navIcon, .displayDiv, footer {
border: 0px solid red;

}

.menuDiv {
}

.displayDiv {
margin-top: 1%;
}

.displayDiv, footer {
float: right;
width: 85%;
}

footer {
font-size: 14px;
margin-top: 10%;
margin-left: 1%;
margin-bottom: 1%;
text-align: center;
}

footer a, footer a:visited {
color: #818181;
}

footer a:hover {
color: #111;
}

img {
width: 100%;
border: 1px solid black
}

.closeTag {
font-size: 70px;
margin-bottom: 10%;
}


.navIcon, .closeTag, .sectionTitle {
font-family: 'Bangers', cursive !important;
font-weight: lighter; 
}

.navIcon {
font-size: 25px;
width: 15%;
position: fixed;
z-index: 1;
margin-top: 1%;
}

.navIcon::after {
content: ' Menu';
}


#colorIconBlk {
color: black;
}

#colorIconRd {
color: #9e0012;
}

#colorIconGrn {
color: #2d8e39;
}

#colorIconBlu {
color: #00329e;
}

.sectionTitle {
font-size: 30px;
margin-bottom: 5%;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

  .navIcon {
  margin-left: -3%;
  }

  footer {
    margin-top: 55%;
  }

}


@media only screen and (min-device-width: 481px) and (max-device-width: 850px) {

  .navIcon {
  margin-left: 1%;
  }

}


@media only screen and (min-device-width: 0px) and (max-device-width: 850px) {

  .navIcon::after{
  content: '';
  }

  .sectionTitle {
  font-size: 20px;
  margin-bottom: 5%;
  }

  .closeTag {
  font-size: 40px;
  text-align: center;
  }

  footer {
  font-size: 10px;
  margin-left: 1%;
  margin-bottom: 1%;
  }

}
